.mainGameplay{
  //padding-top: 24px;
  gap: 65px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  user-select:none;
  @media screen and (min-width: 988px) {
    height: 100%;
  }
  .bodyText{
    position: relative;
    padding: 16px;
    gap: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    //background: white;
    border-radius: 16px;
    border: 3px solid #3A3838;
    background: #000;
    color: #FFFFFF;
    text-align: left;
    font-family: Beeline Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    &.bodyTextUp{
      opacity: 0.7;
    }
    img{
      max-width: 160px;
      pointer-events: none;
      align-items: center
    }
    &:before{
      content: url("../../img/vector.svg");
      position: absolute;
      stroke: #F0B900;;
      top: -30px;
    }
    &:after{
      content: url("../../img/vector.svg");
      transform: rotate(180deg);
      position: absolute;
      bottom: -30px;
    }
  }
  .itHappens{
    cursor: pointer;
    z-index: 100;
    border-radius: 16px;
    padding: 24px;
    text-align: center;

    background: #F0B900;
    color: #202020;
    leading-trim: both;
    text-edge: cap;
    font-family: Beeline Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    &.itHappensUp{
      font-size: 20px;
    }
  }
  .itNotHappens{
    cursor: pointer;
    background: #FFF;
    z-index: 100;
    border-radius: 16px;
    padding: 24px;
    text-align: center;

    color: black;
    leading-trim: both;
    text-edge: cap;
    font-family: Beeline Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    &.itNotHappensUp{
      font-size: 20px;
    }
  }
  @media screen and (max-width: 320px){
    .itNotHappens, .itHappens{
      padding: 16px;
    }
  }
}
