.finalContainer{
  display: flex;
  z-index: 2;
  justify-content: space-between;
  align-items: flex-end;
  gap: 16px;
  padding-bottom: 100px;
  flex-direction: column;
  margin-top: 24px;
  button{
    cursor: pointer;
  }

  .wrap{
    padding: 16px;
    border-radius: 16px;
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 24px;
    background: black;
    border: 3px solid #3A3838;
    &:first-child{
      border: inherit;
      background: white;
      .title{
        color: black;
        font-family: Beeline Sans;
        font-size: 20px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
      }
      .text{
        color: black;
        font-family: Beeline Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
      }
    }
    .title{
      color: #FFFFFF;
      font-family: Beeline Sans;
      font-size: 20px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }
    .text{
      color: #FFFFFF;
      font-family: Beeline Sans;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
    .job{
      border-radius: 16px;
      background: #6331C7;
      padding: 12px 32px;

      color: #FFF;
      font-family: Beeline Sans;
      font-size: 18px;
      border: inherit;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      width: max-content;
    }
  }
  .form{

    &.closeForm{
      border: inherit;
      background: inherit;
      width: 100%;
      padding: 16px 0px;
    }
    .title{
      color: #FFFFFF;
      font-family: Beeline Sans;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
    input{
      border: none;
      color: white;
      border-bottom: 1px solid white;
      width: 100%;
      background: black;
      outline:none;
      font-feature-settings: 'clig' off, 'liga' off;
      font-family: Beeline Sans;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: 24px; /* 133.333% */
      letter-spacing: 0.5px;
    }
    .acceptedBody {
      display: flex;
      gap: 10px;
      .check {
        cursor: pointer;
        min-width: 14px;
        max-width: 14px;
        min-height: 14px;
        max-height: 14px;
        border-radius: 100px;
        border: 1px solid #FFC800;

        &.accepted {
          background: #FFC800;
        }
      }
      .text{
        color: var(--project-text-secondary-text, #757575);
        font-feature-settings: 'clig' off, 'liga' off;
        font-family: Beeline Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 18px; /* 112.5% */
        letter-spacing: -0.078px;
        a{
          color: #757575;
          text-decoration: underline;
          cursor:pointer;
        }
      }
    }
    .next{
      color: #171818;
      font-family: Beeline Sans;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;

      padding: 12px 32px;
      border-radius: 16px;
      width: max-content;
      background: #FFC800;
      border: none;
      min-width: 252px;
      @media screen and (max-width: 320px){
        min-width: 240px;
      }
      &.disableForm{
        cursor: default;
        background: #D0D0D0;
      }
    }
  }
}
