.totalPageContainer{
    display: flex;
    max-width: 450px;
    position: relative;
    height: 100vh; /* фолбэк для браузеров, которые не поддерживают пользовательские свойства */
    //height: calc(var(--vh, 1vh) * 100);
    //padding-top: 40px;
    overflow: hidden;
    width: calc(100vw - 48px);
    padding: 0 24px 24px 24px;
    flex-direction: column;
    justify-content: space-between;
    button{
        cursor: pointer;
    }

    .mainText{
        display: flex;
        flex-direction: column;
        align-items: center;

        z-index: 2;
        min-width: 277px;
        color: white;
        height: 100%;
        margin-top: 50px;
        font-family: Beeline Sans;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        display: flex;
        flex-direction: column;
        //background: white;
        padding: 24px;
        .count{
            color: #FFF;
            text-align: center;
            font-family: Beeline Sans;
            font-size: 32px;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            margin-bottom: 30px;
        }
        .result{
            margin-bottom: 20px;
            color: #FFF;
            text-align: center;
            font-family: Beeline Sans;
            font-size: 26px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
        }
        a{
            text-decoration: inherit;
        }
        button{
            display: flex;
            align-items: center;
            max-width: max-content;
            color: #171818;
            leading-trim: both;
            text-edge: cap;
            font-family: Beeline Sans;
            font-size: 18px;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            background: #F0B900;
            margin-bottom: 52px;
            border:inherit;
            border-radius: 16px;
            z-index: 100;
            padding: 15px 64px;
            letter-spacing: 0;
            text-align: left;

            img{
                max-width: 40px;
            }
        }
        img{
            max-width: 120px;
        }
    }
    .imgDragon{
        display: flex;
        justify-content: center;
        position: relative;
        .dragon{
            z-index: 2;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            max-width: 240px;
            margin: 0 auto;
            @media screen and (max-width: 320px){
                width: 220px;
            }
        }
        a{z-index: 100;}

    }
}
