.InterruptionContainer{
  height: 100%;
  gap: 65px;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  button{
    cursor: pointer;
  }
  .wrap{
    padding: 16px;
    align-items: center;
    display: flex;
    border-radius: 16px;
    flex-direction: column;
    gap: 24px;
    background: white;
    border: 3px solid #3A3838;
    &:first-child{
      background: black;
      border: 3px solid #3A3838;
      .text{
        color: white;
      }
    }

    .textNext{
      color: black;
      font-family: Beeline Sans;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
    .job{
      color: #171818;
      font-family: Beeline Sans;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      border-radius: 16px;
      padding: 12px 32px;
      background: #F0B900;
      border: none;
      width: max-content;
    }
    .next{
      min-width: 251px;
      padding: 12px 32px;
      border-radius: 16px;
      width: max-content;
      background: #FFC800;
      border: inherit;
      color: black;
      font-family: Beeline Sans;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      @media screen and (max-width: 320px){
        min-width: 230px;
      }
    }
  }
}
