.mainPageContainer{
  display: flex;
  position: relative;
  height: 100vh; /* фолбэк для браузеров, которые не поддерживают пользовательские свойства */
  //height: calc(var(--vh, 1vh) * 100);
  //padding-top: 40px;
  overflow: hidden;
  padding: 0 24px 24px 24px;
  flex-direction: column;
  justify-content: space-between;
  button{
    cursor: pointer;
  }
  .bg{
    background: url("../../img/background.svg");
    position: absolute;

    left: -143px;
    width: 600px;
    top: -103px;
    z-index: 1;
    height: 604px;
  }
  .mainText{
    z-index: 2;
    color: white;
    height: 100%;
    margin-top: 24px;
    font-family: Beeline Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    display: flex;
    flex-direction: column;
    //background: white;
    padding: 24px;
    gap: 24px;
    img{
      max-width: 120px;
    }
  }
  .imgDragon{
    display: flex;
    justify-content: center;
    position: relative;
    .dragon{
      z-index: 2;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      max-width: 240px;
      margin: 0 auto;
      @media screen and (max-width: 320px){
        width: 220px;
      }
    }
    a{z-index: 100;}
    button{
      color: #171818;
      leading-trim: both;
      text-edge: cap;
      font-family: Beeline Sans;
      font-size: 18px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      background: #F0B900;
      margin-bottom: 52px;
      border:inherit;
      border-radius: 16px;
      z-index: 100;
      padding: 15px 64px;
      letter-spacing: 0;
      text-align: left;

      img{
        max-width: 40px;
      }
    }
  }
}
