.factsBody{
  height: 100%;
  gap: 65px;
  display: flex;
  z-index: 2;
  flex-direction: column;
  justify-content: center;
  .containerResult{
    display: flex;
    flex-direction:column;
    gap: 15px;
  }
  .answer{
    color: #FFF;

    font-family: Beeline Sans;
    font-size: 26px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    width: max-content;
    border-bottom: 1px solid #F0B900;
  }
  .bodyResult{
    border-radius: 16px;
    border: 3px solid #3A3838;
    color: #FFFFFF;
    leading-trim: both;
    text-edge: cap;
    font-family: Beeline Sans;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    position: relative;
    padding: 16px;
    gap: 8px;

    display: flex;
    flex-direction: column;
    align-items: flex-end;
    img{
      max-width: 160px;
      align-items: center
    }
  }
  .next{
    cursor: pointer;
    border: inherit;
    width: 100%;
    border-radius: 16px;
    padding: 20px 80px;
    background: #F0B900;
    color: #202020;
    leading-trim: both;
    text-edge: cap;
    font-family: Beeline Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
}
